<template>
  <div>
    <van-cell>
      <template #title>
        <div class="title-box" :class="{ type3: cover.type === 3 }">
          <span>{{ title }}</span>
          <div class="img-box">
            <img v-lazy="imgUrl" alt="" v-for="(imgUrl, index) in cover.images" :key="index" />
          </div>
        </div>
      </template>

      <template #label>
        <div class="label-box">
          <div>
            <span>{{ author }}</span>
            <span>{{ commCount }} 评论</span>
            <span>{{ timeAgo }}</span>
          </div>
          <!-- 点击叉号弹出模态面板 -->
          <van-icon name="cross" v-if="isCrossShow" @click.stop="showActionSheet" />
        </div>
      </template>
    </van-cell>
    <!-- 叉号模态面板 -->
    <van-action-sheet
      v-model="show"
      :actions="actions"
      get-container="body"
      :cancel-text="cancelText"
      @select="onSelect"
      @cancel="onCancel"
    />
  </div>
</template>

<script>
import { timeAgo } from '@/utils/date'
import { firstActions, secondActions } from '@/api/report'
import { Notify } from 'vant'
export default {
  name: 'ArticleItem',
  data() {
    return {
      show: false,
      actions: firstActions,
      cancelText: '取消'
    }
  },
  props: {
    isCrossShow: {
      // 叉号显示与隐藏
      type: Boolean,
      default: true
    },
    id: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    author: {
      type: String,
      default: ''
    },
    commCount: {
      type: Number,
      default: 0
    },
    pubdate: {
      type: String,
      default: ''
    },
    cover: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  methods: {
    showActionSheet() {
      // 重置数据
      this.actions = firstActions
      this.cancelText = '取消'
      this.show = true
    },
    async onSelect(action, index) {
      if (action.name === '反馈垃圾内容') {
        this.actions = secondActions
        this.cancelText = '返回'
      } else if (action.name === '不感兴趣') {
        this.show = false
        const res = await this.$API.reqDislike(this.id)
        if (res.status === 201) {
          Notify({ type: 'success', message: '反馈成功' })
        }
      } else {
        this.show = false
        const res = await this.$API.reqReports(this.id, action.value)
        if (res.status === 201) {
          Notify({ type: 'success', message: '举报成功' })
        }
      }
    },
    // 模态面板取消按钮的回调
    onCancel() {
      if (this.cancelText === '返回') {
        this.actions = firstActions
        this.cancelText = '取消'
        this.show = true
      }
    }
  },
  computed: {
    timeAgo() {
      return timeAgo(this.pubdate)
    }
  }
}
</script>

<style lang="less" scoped>
.title-box {
  display: flex;
  justify-content: space-between;
  .img-box {
    display: flex;
    justify-content: space-between;
    img {
      width: 113px;
      height: 70px;
    }
  }
}
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  span {
    margin: 0 3px;
  }
  span:nth-child(1) {
    margin-left: 0;
  }
}
.type3 {
  flex-direction: column;
}
// /deep/ .van-popup--bottom {
//   bottom: 100px !important;
// }
</style>
